import React from 'react';
 const ProgressBar = ({ width, percent ,finished,setfinished,textBeforKey,orderKey}) => {

  const [value, setValue] = React.useState(0);


  React.useEffect(() => {
    setValue(percent * width);
    setTimeout(function (){

//  setfinished(true);

}, 2500);

  });

  return (
    <>
  {!finished   && <div>
      <div className="progress-div" style={{ width: width }}>
        <div style={{ width: `${value}px` }} className="progress" />
      </div>
    </div>}
    {finished && <div>
        <span style={{fontWeight:"bold"}}>{textBeforKey?textBeforKey:'Order placed'} #{orderKey}</span>
      </div>}
    </>
  );
};

export default ProgressBar;
