import React, {
  ChangeEvent,
  FC,
  useCallback,
  useEffect,
  useState,
  useRef,
  useContext,
} from "react";
import "../styles/multiRangeSlider.css";
import AppContext from "../context/AppContext";

const MultiRangeSlider = ({ min, max, onChangeValue }) => {
  function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  const {
    orderBy,
    setorderBy,
    setactionsVisible,
    greaterthan,
    setgreaterthan,
    lessthan,
    setlessthan,
  } = useContext(AppContext);

  const [minVal, setMinVal] = useState(min);
  const [maxVal, setMaxVal] = useState(max);
  const minValRef = useRef(min);
  const maxValRef = useRef(max);
  const range = useRef(null);
  //
  // Convert to percentage
  const getPercent = useCallback(
    (value: number) => Math.round(((value - min) / (max - min)) * 100),
    [min, max]
  );
  //
  // Set width of the range to decrease from the left side

  useEffect(() => {
    //setMaxVal(max);
    const minPercent = getPercent(minVal);
    const maxPercent = getPercent(maxValRef.current);

    if (range.current) {
      range.current.style.left = `${minPercent}%`;
      range.current.style.width = `${maxPercent - minPercent}%`;
    }
    setgreaterthan(minVal);
  }, [minVal, getPercent, max]);
  //
  // Set width of the range to decrease from the right side

  useEffect(() => {
    setMaxVal(max);
    const minPercent = getPercent(minValRef.current);
    const maxPercent = getPercent(maxVal);

    if (range.current) {
      range.current.style.width = `${maxPercent - minPercent}%`;
    }
  }, [max]);

  useEffect(() => {
    //    setMaxVal(max);
    const minPercent = getPercent(minValRef.current);
    const maxPercent = getPercent(maxVal);

    if (range.current) {
      range.current.style.width = `${maxPercent - minPercent}%`;
    }
    setlessthan(maxVal);
  }, [maxVal, getPercent, max]);

  return (
    <div
      className="rangeContainer"
      style={{ height: "50px", marginTop: "20px" }}
    >
      <input
        type="range"
        step={500}
        min={min}
        max={max}
        value={minVal}
        onChange={(event) => {
          const value = Math.min(Number(event.target.value), maxVal);
          setMinVal(value);
          minValRef.current = value;
          onChangeValue(minVal, maxVal);
        }}
        className="thumb thumb--left"
        style={{
          zIndex: minVal > max - 100 && "5",
          maxWidth: "400px",
          width: " 80%",
        }}
      />
      <input
        type="range"
        step={500}
        min={min}
        max={max}
        value={maxVal}
        onChange={(event) => {
          const value = Math.max(Number(event.target.value), minVal);
          setMaxVal(value);
          maxValRef.current = value;
          onChangeValue(minVal, maxVal);
        }}
        className="thumb thumb--right"
        style={{ maxWidth: "400px", width: " 80%" }}
      />

      <div className="slider">
        <div className="slider__left-value">
          LBP{numberWithCommas(minVal)} • KG
        </div>
        <div className="slider__right-value">
          LBP{numberWithCommas(maxVal)} • KG
        </div>
        <div className="slider__track"></div>
        <div ref={range} className="slider__range"></div>
      </div>
    </div>
  );
};

export default MultiRangeSlider;
