import React,{useState,useContext,useEffect} from "react";
import AppContext from '../context/AppContext';

import Icon from '../components/Icon';
import Select from 'react-select';

const optionss = [
{ value: 'today', label: 'Today' },
{ value: 'tommorow', label: 'Tommorow' },
{ value: 'Jul17', label: 'July 17' }
]

function SelectInput({text,icon,className,placeholder,onChange,onMenuOpen,defaultValue,value,inputValue,setvalue,style,options=optionss}) {
  const {auth,setauth,visible,setvisible,actionsVisible,setactionsVisible,deliverTo,setdeliverTo} = useContext(AppContext);


  const [borderColor, setborderColor] = React.useState("#E0E0E0");
const onFocus =() =>{
  setborderColor("#000");
}
const onBlur =() =>{
  setborderColor("#F6F6F6");
}
const customStyles = {
  control: base => ({
    ...base,
    ...style,
    border: 0,
    backgroundColor:"#F6F6F6",
    borderBottom:"2px solid #E0E0E0",
    borderRadius:"0px",
    padding:"10px",
    // This line disable the blue border
    boxShadow: 'none',
    '&:hover': {
    borderBottom:"2px solid #000",
   }
 }),
 option: (styles, { data, isDisabled, isFocused, isSelected }) => {
     // const color = chroma(data.color);
     //console.log({ data, isDisabled, isFocused, isSelected });
     return {
       ...styles,
       backgroundColor: isFocused ? "#F6F6F6" : null,
       color: "#333333"
     };
   },

};

const onCustomChange = (v) =>{
 
  if(v)
{  onChange(v);}else{
  onChange("")
}

}

  return (
    <Select onMenuOpen={onMenuOpen}   className="menu-outer-top" defaultValue={defaultValue} isClearable={true}  styles={customStyles} options={options}  placeholder={placeholder} onChange={(v)=>{onCustomChange(v);}} value={inputValue} />

  );
}

export default SelectInput;
