import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import Icon from "../components/Icon";
import Button from "../components/Button";
import QuantityButton from "../components/QuantityButton";

import AppContext from "../context/AppContext";

// <QuantityButton text="$3 / KG"  rounded={true} className="qunatitybtnCard"/>
function LandCard({
  text,
  title,
  price,
  grade,
  supply,
  moreInfo,
  row,
  image,
  isFav,
  onFavClicked,
  ondeleteClicked,
  onButtonClicked,
  showFav = true,
}) {
  const history = useHistory();
  const {
    currency,
    quantity,
    setQuantity,
    totalPrice,
    setTotalPrice,
    items,
    setItems,
    setSelectedItem,
    role,
  } = useContext(AppContext);

  const [showchilds, setshowchilds] = useState(false);
  const [favorite, setFavorite] = useState(isFav);
  const oncardclick = () => {
    setshowchilds(!showchilds);
  };

  const onClick = (farmer) => {
    //setQuantity(quantity + 100);
    //setTotalPrice(totalPrice + (price ));
    const found = items.filter((rowProduct) => {
      if (rowProduct.id === row.id) {
        return rowProduct;
      }
    });
    if (found.length === 0) {
      const modifiedItem = {
        ...row,
        quantity: 100,
        remainingQuantity: 0,
        location: "No delivery location",
        farmer: farmer,
      };
      setItems([...items, modifiedItem]);
      setSelectedItem(modifiedItem);
      //  console.log(modifiedItem);
    } else {
      setSelectedItem(found[0]);
    }

    //history.push(`/checkout`);
    history.push(`/item/${row.id}`);
  };

  function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  return (
    <div style={{ position: "relative" }}>
      {role && role == "admin" && (
        <div
          style={{
            position: "absolute",
            top: "20px",
            right: "60px",
            cursor: "pointer",
          }}
          onClick={() => {
            history.push(`/product/${row.id}`);
          }}
        >
          {" "}
          <Icon name="edit" size={22} />
        </div>
      )}

      {role && role == "admin" && (
        <div
          style={{
            position: "absolute",
            top: "20px",
            right: "100px",
            cursor: "pointer",
          }}
          onClick={() => {
            ondeleteClicked();
          }}
        >
          {" "}
          <Icon name="trash" size={22} />
        </div>
      )}
      <div
        className="cardMain"
        style={{
          display: "flex",
          flexDirection: "column",
          backgroundColor: "#f8f9fb",
        }}
      >
        <div style={{ display: "flex", flexDirection: "row", flex: 1 }}>
          <div
            style={{
              display: "flex",
              marginTop: "15px",
              padding: "15px",
              flex: 3,
            }}
          >
            <div
              className=""
              style={{ flex: 4, display: "flex", flexDirection: "column" }}
            >
              <span className="heading" style={{ flex: 1 }}>
                {title}
              </span>

              <div
                className="headingIcon"
                style={{ display: "flex", alighItems: "center", flex: 1 }}
              >
                <div
                  style={{
                    display: "flex",
                    alighItems: "center",
                    flexDirection: "column",
                  }}
                >
                  <div
                    className="headingIconText"
                    style={{ fontSize: "16px", lineHeight: "28px" }}
                  >{`${grade} `}</div>
                  <span
                    style={{
                      fontSize: "14px",
                      lineHeight: "18px",
                      marginBottom: "15px",
                    }}
                  >
                    {moreInfo}
                  </span>
                </div>
              </div>
              <div style={{ flex: 1 }}></div>
            </div>
          </div>
        </div>

        <div
          className="cardBtnDiv"
          style={{
            flex: 1,
            padding: "0px 5px",
            marginTop: "0px",
            cursor: "pointer",
          }}
          onClick={oncardclick}
        >
          <div style={{ flex: 5 }}></div>
          <div style={{ display: "flex" }}>
            <Button
              text="Manage stock"
              style={{ padding: "10px" }}
              onClick={onButtonClicked}
              icon="box"
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default LandCard;
