import React, { useState, useContext, useRef, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";

import AppContext from "../context/AppContext";
import AuthContext from "../AuthProvider";
import Screen from "../components/Screen";
import Card from "../components/Card";
import MultiSelect from "../components/MultiSelect";
import ModalOption from "../components/ModalOption";

import TextInput from "../components/TextInput";
import TextOption from "../components/TextOption";
import SelectInput from "../components/SelectInput";
import MultiRangeSlider from "../components/MultiRangeSlider";
import Icon from "../components/Icon";
import MultiTextOption from "../components/MultiTextOption";

import GoogleMapReact from "google-map-react";

import Modal from "react-modal";

import firebase from "../firebase";
import "firebase/firestore";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    minWidth: "450px",
    padding: "24px",
  },
};

// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
//Modal.setAppElement('#root');

export default function Item() {
  //const ref = firebase.firestore().collection("restaurant_orders");
  const { user } = useContext(AuthContext);

  const {
    auth,
    setauth,
    visible,
    setvisible,
    actionsVisible,
    setactionsVisible,
    deliverTo,
    setdeliverTo,
    totalPrice,
    setTotalPrice,
    currency,
    items,
    setItems,
    deliveryTotal,
    setdeliveryTotal,
    selectedItem,
    setSelectedItem,
    setsplitDelivery,
  } = useContext(AppContext);

  let { id } = useParams();

  const history = useHistory();

  useEffect(() => {
    if (!selectedItem) {
      history.push("/");
    }
  }, [selectedItem]);

  const ref = firebase.firestore().collection("vendor_products");

  useEffect(async () => {
    if (id) {
      await ref
        .where("id", "==", id)
        .get()
        .then((res) => {
          res.forEach((result) => {
            let data = result.data();
            setSelectedItem({ ...selectedItem, ...data });
          });
        });
    }
  }, []);
  const [opened, setopened] = useState(false);
  const [frame, setframe] = useState("main");

  const producQuantityTempChange = (event, change, row) => {
    let quanvalue =
      change == "+" ? selectedItem.quantity + 50 : selectedItem.quantity - 50;
    if (quanvalue <= 0) {
      quanvalue = 50;
    }
    const modifiedItemNow = { ...selectedItem, quantity: quanvalue };

    setSelectedItem(modifiedItemNow);
    const newItems = items.map((rr) => {
      if (rr.id === modifiedItemNow.id) {
        return modifiedItemNow;
      } else {
        return rr;
      }
    });
    setItems(newItems);
  };
  function numberWithCommas(x) {
    return x?.toString()?.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  if (!selectedItem) {
    return <></>;
  } else {
    return (
      <>
        <ModalOption
          modalIsVisible={opened}
          setmodalIsVisible={setopened}
          frameUsed={frame}
          setframeUsed={setframe}
        />

        <div
          className="container-flex row"
          style={{ background: "#fff", padding: "0px", marginTop: "130px" }}
        >
          <div
            className="col-md-4 col-lg-4 col-sm-12 col-xs-12"
            style={{
              flex: 2,
              minWidth: "300px",
              padding: "40px 100px 40px 40px",
            }}
          >
            <div style={{ display: "flex", alignItems: "center" }}>
              <span
                style={{
                  fontSize: "36px",
                  lineHeight: "44px",
                  fontWeight: "bold",
                  flex: 3,
                  paddingLeft: "10px",
                  paddingBottom: "10px",
                }}
              >
                {" "}
                {selectedItem.name}
              </span>
            </div>

            <div className="cardImage" style={{ flex: 1 }}>
              <img
                className="imageDiv"
                src={selectedItem.photo}
                style={{ width: "auto" }}
              />
            </div>
            <div className="OptionSection">
              <TextOption
                onClick={() => {
                  setopened(true);
                  setframe("price");
                }}
                iconAlign="left"
                icon="box"
                text={`Grade ${selectedItem.grade}`}
              />
            </div>
            <div className="OptionSection">
              <TextOption
                iconAlign="left"
                noBack={true}
                icon="package"
                text="Quantity"
                subText={`${selectedItem.quantity} KG`}
                withBorder={true}
                secondAction="plus"
                thirdAction="minus"
                onClicksecondAction={(e) =>
                  producQuantityTempChange(e, "+", selectedItem)
                }
                onClickthirdAction={(e) =>
                  producQuantityTempChange(e, "-", selectedItem)
                }
              />
            </div>

            <div className="OptionSection">
              <TextOption
                onClick={() => {
                  setopened(true);
                  setframe("schedule");
                }}
                iconAlign="left"
                icon="clock"
                text="Schedule"
                subText=""
                btnText="Edit"
                frameUsed="schedule"
              />
            </div>
            <div
              style={{
                borderBottom: "2px solid rgb(226, 226, 226)",
                marginTop: "-2px",
              }}
            ></div>

            <div
              style={{
                display: "flex",
                flexDirection: "row",
                marginTop: "24px",
              }}
            >
              <span
                style={{
                  flex: 1,
                  fontSize: "20px",
                  lineHeight: "28px",
                  fontWeight: 500,
                }}
              >
                Farmers
              </span>
              <span
                style={{
                  marginLeft: "auto",
                  fontSize: "20px",
                  lineHeight: "28px",
                  fontWeight: 500,
                }}
              ></span>
            </div>
            <div className="OptionSection">
              <TextOption
                onClick={() => {
                  setopened(true);
                  setframe("farmers");
                }}
                iconAlign="left"
                icon="users"
                text="Choose farmer"
                subText={
                  selectedItem?.farmerChosen
                    ? selectedItem?.farmerChosen?.label
                    : "We choosed our best farmers for you"
                }
                btnText="Edit"
                frameUsed="payment"
              />
            </div>

            <div
              style={{
                display: "flex",
                flexDirection: "row",
                marginTop: "24px",
              }}
            >
              <span
                style={{
                  flex: 1,
                  fontSize: "20px",
                  lineHeight: "28px",
                  fontWeight: 500,
                }}
              >
                Delivery locations
              </span>
              <span
                style={{
                  marginLeft: "auto",
                  fontSize: "20px",
                  lineHeight: "28px",
                  fontWeight: 500,
                }}
              ></span>
            </div>
            <div className="OptionSection">
              <TextOption
                onClick={() => {
                  setopened(true);
                  setsplitDelivery(true);
                  setframe("deliveryTo");
                }}
                iconAlign="left"
                icon="map"
                text={"Multiple locations"}
                subText=""
                btnText="Edit"
                frameUsed="payment"
              />
            </div>

            <div
              style={{
                borderBottom: "2px solid rgb(226, 226, 226)",
                marginTop: "-2px",
              }}
            ></div>

            <div
              style={{
                borderBottom: "2px solid rgb(226, 226, 226)",
                marginTop: "-2px",
              }}
            ></div>

            <div style={{ marginTop: "20px" }}>
              <TextInput
                className="textOption"
                placeholder="Add a note on the product ..."
                icon=""
              />
            </div>
          </div>

          <div
            style={{
              flex: 1,
              background: "rgb(246, 246, 246)",
              padding: "80px 80px",
              minWidth: "300px",
            }}
            className="rightMain col-md-8 col-lg-8 col-sm-12 col-xs-12"
          >
            <div style={{ position: "sticky", top: 140 }}>
              <ul>
                <li>
                  <div className="listtable">
                    <div className="listText" style={{ fontWeight: "bold" }}>
                      Price
                    </div>
                  </div>
                  <div className="listText" style={{ fontWeight: "bold" }}>
                    <span>{`LBP${numberWithCommas(
                      selectedItem.farmerChosen.value.price
                    )} • KG`}</span>
                  </div>
                </li>

                <li className="spacerH"></li>

                <li>
                  <div className="listtable">
                    <div className="listText" style={{ fontWeight: "bold" }}>
                      Total Price
                    </div>
                  </div>
                  <div className="listText" style={{ fontWeight: "bold" }}>
                    <span>{`LBP${numberWithCommas(
                        selectedItem.farmerChosen.value.price * selectedItem.quantity
                    )}`}</span>
                  </div>
                </li>
              </ul>
              <div
                className="blackbtn"
                onClick={() => history.push("/checkout")}
              >
                Proceed to checkout
              </div>
              <div className="info"> </div>

              <hr className="separator" style={{ marginBottom: "5px" }} />
            </div>
          </div>
        </div>
      </>
    );
  }
}
