import React, { useContext, useState } from "react";
import Screen from "../components/Screen";
import Button from "../components/Button";
import TextInput from "../components/TextInput";
import AppContext from "../context/AppContext";
import "react-phone-number-input/style.css";
import { useHistory } from "react-router-dom";
import { User, auth } from "firebase";
import firebase from "../firebase";
import "firebase/auth";
import "firebase/firestore";
import AuthContext from "../AuthProvider";

const Login = () => {
  const [errorss, seterrorss] = useState({});
  const [showForgotPassword, setshowForgotPassword] = useState(false);
  const {
    deliveryLocations,
    setdeliveryLocations,
    setRole,
    setUserName,
    setUserEmail,
  } = useContext(AppContext);

  const authContext = useContext(AuthContext);
  const { loadingAuthState, setLoadingAuthState, user } =
    useContext(AuthContext);
  const history = useHistory();
  const [values, setValues] = useState({
    email: "",
    password: "",
  });
  const handleClick = () => {
    history.push("/register");
  };
  const handleChange = (event) => {
    event.persist();
    setValues((values) => ({
      ...values,
      [event.target.name]: event.target.value,
    }));
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    if (!handleValidation()) {
      return;
    }
    setLoadingAuthState(true);
    firebase
      .auth()
      .signInWithEmailAndPassword(values.email, values.password)
      .then((res) => {
        authContext.setUser(res);
        //console.log(res);
        try {
          const db = firebase.firestore();
          db.collection("Users")
            .doc(res.user.uid)
            .get()
            .then((querySnapshot) => {
              setdeliveryLocations(querySnapshot.data().deliveryLocations);
              const role = querySnapshot.data().role;
              setRole(role);
              setUserName(querySnapshot.data().username);

              setUserEmail(querySnapshot.data().email);

              //console.log(querySnapshot.data());
            });
        } catch (e) {
          console.log(e);
        }

        //    console.log(res, 'res')
        //  history.push("/dashboard");
      })
      .catch((error) => {
        setLoadingAuthState(false);
        //    alert(error.message);
        let errorsNew = {};
        errorsNew["password"] = "Incorrect sign in attempt";
        seterrorss(errorsNew);
        console.log(error.message);
      });
  };

  const handleValidation = () => {
    let fields = values;
    let errors = {};
    let formIsValid = true;

    if (!fields["email"]) {
      formIsValid = false;
      errors["email"] = "Cannot be empty";
    }

    if (!fields["password"]) {
      formIsValid = false;
      errors["password"] = "Cannot be empty";
    }

    if (typeof fields["email"] !== "undefined") {
      let lastAtPos = fields["email"].lastIndexOf("@");
      let lastDotPos = fields["email"].lastIndexOf(".");

      if (
        !(
          lastAtPos < lastDotPos &&
          lastAtPos > 0 &&
          fields["email"].indexOf("@@") == -1 &&
          lastDotPos > 2 &&
          fields["email"].length - lastDotPos > 2
        )
      ) {
        formIsValid = false;
        errors["email"] = "Email is not valid";
      }
    }

    seterrorss(errors);
    return formIsValid;
  };
  const { setauth, setvisible, setactionsVisible } = useContext(AppContext);

  const forgotPassword = (Email) => {
    firebase
      .auth()
      .sendPasswordResetEmail(values.email)
      .then(function () {
        let errorsNew = {};
        errorsNew["sentEmail"] = "Check your email";
        seterrorss(errorsNew);
      })
      .catch(function (e) {
        let errorsNew = {};
        errorsNew["password"] = "Try again later !";
        seterrorss(errorsNew);
        console.log(e.message);
      });
  };
  return (
    <div className="portable">
      <img
        src={require("../assets/images/logo1.png").default}
        style={{ flex: 2, width: "80px", marginLeft: "-17px" }}
      />

      <h6 style={{ marginTop: "10px", marginBottom: "20px", fontSize: "25px" }}>
        Welcome back
      </h6>
      <br />
      <label>Sign in with your account.</label>
      {errorss["incorrect"] && (
        <span className="error">{errorss["incorrect"]}</span>
      )}

      <form onSubmit={handleSubmit}>
        <div style={{ marginBottom: "10px" }}>
          <TextInput
            placeholder="Enter email address"
            onChange={handleChange}
            text={values.email}
            name="email"
            autocomplete="email"
          />
          {errorss["email"] && (
            <span className="error">{errorss["email"]}</span>
          )}
          {errorss["sentEmail"] && (
            <span className="error infoMessage">{errorss["sentEmail"]}</span>
          )}
        </div>

        {!showForgotPassword && (
          <div>
            <TextInput
              type="password"
              placeholder="Enter password"
              onChange={handleChange}
              text={values.password}
              name="password"
              autocomplete="current-password"
            />
            {errorss["password"] && (
              <span className="error">{errorss["password"]}</span>
            )}
          </div>
        )}
      </form>
      {showForgotPassword && (
        <Button
          onClick={(e) => {
            forgotPassword(e);
          }}
          style={{ marginTop: "15px" }}
          text="Send reset email"
          rounded={false}
        />
      )}

      {!showForgotPassword && (
        <Button
          onClick={(e) => {
            handleSubmit(e);
          }}
          style={{ marginTop: "15px" }}
          text="Sign in"
          rounded={false}
        />
      )}
      <div className="textCenter" style={{ marginTop: "8px" }}>
        {!showForgotPassword && (
          <label
            onClick={() => {
              setshowForgotPassword(true);
            }}
          >
            {" "}
            <span style={{ color: "#02a78b", cursor: "pointer" }}>
              Forgot password?
            </span>
          </label>
        )}

        {!showForgotPassword && (
          <label onClick={handleClick}>
            New to Agricom?{" "}
            <span style={{ color: "#02a78b", cursor: "pointer" }}>
              Create an account
            </span>
          </label>
        )}
        {showForgotPassword && (
          <label
            onClick={() => {
              setshowForgotPassword(false);
              seterrorss([]);
            }}
          >
            {" "}
            <span style={{ color: "#02a78b", cursor: "pointer" }}>Sign in</span>
          </label>
        )}
      </div>
    </div>
  );
};

export default Login;
