import React, { useState, useContext, useEffect } from "react";
import AppContext from "../context/AppContext";

import Icon from "../components/Icon";
import Select from "react-select";

const optionss = [
  { value: "price-asc", label: "Price: High to Low" },
  { value: "price-desc", label: "Price: Low to High" },
];

function RoundedSelectInput({
  text,
  icon,
  className,
  placeholder,
  onChange,
  defaultValue,
  value,
  options = optionss,
}) {
  const {
    auth,
    setauth,
    visible,
    setvisible,
    actionsVisible,
    setactionsVisible,
    deliverTo,
    setdeliverTo,
  } = useContext(AppContext);

  const [borderColor, setborderColor] = React.useState("#E0E0E0");
  const onFocus = () => {
    setborderColor("#000");
  };
  const onBlur = () => {
    setborderColor("#F6F6F6");
  };
  const customStyles = {
    control: (base) => ({
      ...base,
      color: "#484848",
      backgroundColor: "#FFF",
      border: "1px solid #eae0da",
      borderRadius: "120px",
      padding: "8px 5px",
      // This line disable the blue border
      boxShadow: "none",
      "&:hover": {
        border: "1px solid #eae0da",
      },
    }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      // const color = chroma(data.color);
      //console.log({ data, isDisabled, isFocused, isSelected });
      return {
        ...styles,
        backgroundColor: isFocused ? "#F6F6F6" : null,
        color: "#333333",
      };
    },
  };
  return (
    <Select
      value={value}
      defaultValue={defaultValue}
      isClearable={true}
      styles={customStyles}
      options={options}
      placeholder={placeholder}
      onChange={onChange}
    />
  );
}

export default RoundedSelectInput;
