import ".././App.css";
import Icon from "../components/Icon";
function Button({ text, onClick, className, style, icon, rounded = false }) {
  const roundBtn = { borderRadius: "80px" }; //rounded ? {borderRadius:'80px'} : {};
  return (
    <button
      className={`btnMain ${className}`}
      onClick={onClick}
      style={(roundBtn, style)}
    >
      {icon && (
        <Icon
          style={{ color: "#E5E5E4" }}
          className="clickable"
          name={icon}
          size={20}
          color="#E5E5E4"
        />
      )}
      <p className="p-0 m-0">{text}</p>
    </button>
  );
}

export default Button;
