import FeatherIcon from 'feather-icons-react';
import '.././App.css';

function Icon({name,onClick,className,style,color="#000",background="",fill="#fff",size=20,justifyContent="center"}) {
  return (
    <div style={{display:"flex",justifyContent:justifyContent,padding:"0px 5px"}} className={`${background}`}  onClick={onClick}>
    <FeatherIcon color={color}  icon={name}  size={size}  fill={fill}/>
    </div>
  );
}

export default Icon;
