  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    //  subtitle.style.color = '#f00';
  }

  function closeModal() {
    setIsOpen(false);
  }

  function priceChange(value, highValue) {
    changedFilter();
  }

  function sortByChange() {
    const ref = firebase
      .firestore()
      .collection("vendor_products")
      .where("price", ">=", greaterthan)
      .where("price", "<=", lessthan)
      //.where('vendorID', 'in', ['4ULfql4vdNyE1Zb7VWM5'])
      .orderBy(orderBy.split("-")[0], orderBy.split("-")[1]);

    unsubscribe.current = ref.onSnapshot(onCollectionUpdate);
  }

  function changedFilter() {
    const categpriesApplied = arrayfilter.length !== 0 ? true : false;
    const qualityApplied = qualityarrayfilter.length !== 0 ? true : false;
    let ref = firebase
      .firestore()
      .collection("vendor_products")
      .where("price", ">=", greaterthan)
      .where("price", "<=", lessthan)
      .where(
        categpriesApplied ? "category" : "price",
        categpriesApplied ? "in" : ">=",
        categpriesApplied ? arrayfilter : 0
      );
    unsubscribe.current = ref.onSnapshot(onCollectionUpdate);
  }

  // useEffect(()=>{

  //    unsubscribe.current = ref.onSnapshot(onCollectionUpdate);
  // },[searchedText])
  useEffect(() => {
    setactionsVisible(true);
    setsignoutvisible(false);

    try {
      const maxValue = firebase
        .firestore()
        .collection("vendor_products")
        .orderBy("price", "desc")
        .limit(1);
      maxValue.get().then((querySnapshot) => {
        querySnapshot.docs.map((eee) => {
          setmaxValueset(eee.data().price);
        });
      });
    } catch (e) {}
    unsubscribe.current = ref.onSnapshot(onCollectionUpdate);
    return () => {
      unsubscribe.current();
    };
  }, []);

  const onCollectionUpdate = (querySnapshot) => {
    let gata = [];
    const tempData = [];
    querySnapshot.forEach((doc) => {
      const { quality } = doc.data();

      if (qualityarrayfilter && qualityarrayfilter.length !== 0) {
        if (qualityarrayfilter.indexOf(quality) >= 0) {
          tempData.push(doc.data());
        }
      } else {
        tempData.push(doc.data());
      }
    });

    const tempdata2 = [];
    tempData.forEach((doc) => {
      const { grade } = doc;

      if (gradearrayfilter && gradearrayfilter.length !== 0) {
        if (gradearrayfilter.indexOf(grade) >= 0) {
          tempdata2.push(doc);
        }
      } else {
        tempdata2.push(doc);
      }
    });

    const searchdata = [];
    tempdata2.forEach((doc) => {
      const { name } = doc;
      if (searchedText != null && searchedText != "") {
        const text = searchedText != null ? searchedText.toLowerCase() : "";
        const index = name.toLowerCase().search(text);

        if (index !== -1) {
          searchdata.push(doc);
        }
      } else {
        searchdata.push(doc);
      }
    });

    const newdata = [];

    searchdata.forEach((doc) => {
      const { farmers } = doc;
      const { rating } = farmers[0];

      if (ratingarrayfilter && ratingarrayfilter.length !== 0) {
        const contains = ratingarrayfilter.some((element) => {
          if (rating.toString().includes(element)) {
            newdata.push(doc);
          }
        });
      } else {
        newdata.push(doc);
      }
    });

    gata = newdata;

    //   const filteredResults =querySnapshot?.docs?.map((doc) => doc.data());

    setData(gata);

    //setproductCount(querySnapshot.docs.length);
    setproductCount(gata.length);
    setIsLoading(false);
  };

