import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import Icon from "../components/Icon";
import Button from "../components/Button";
import QuantityButton from "../components/QuantityButton";

import AppContext from "../context/AppContext";

// <QuantityButton text="$3 / KG"  rounded={true} className="qunatitybtnCard"/>
function Card({
  text,
  title,
  price,
  grade,
  supply,
  moreInfo,
  row,
  image,
  isFav,
  onFavClicked,
  ondeleteClicked,
  showFav = true,
}) {
  const history = useHistory();
  const {
    currency,
    quantity,
    setQuantity,
    totalPrice,
    setTotalPrice,
    items,
    setItems,
    setSelectedItem,
    selectedItem,
    role,
  } = useContext(AppContext);

  const [showchilds, setshowchilds] = useState(false);
  const [favorite, setFavorite] = useState(isFav);
  const addToFav = "Add to favorites";
  const removeFromFav = "Remove from favorites";
  const oncardclick = () => {
    setshowchilds(!showchilds);
  };

  const onClick = (row,farmer) => {
 
    let farmerChosen = {
      label:farmer.name,
      value:farmer,
      key:farmer.name
    }
    
    //setQuantity(quantity + 100);
    //setTotalPrice(totalPrice + (price ));
    const found = items.filter((rowProduct) => {
      if (rowProduct.id === row.id) {
        return rowProduct;
      }
    });
    if (found.length === 0) {
      const modifiedItem = {
        ...row,
        quantity: 100,
        remainingQuantity: 0,
        location: "No delivery location",
        farmer: farmer,
      };
      setItems([...items, {...modifiedItem,farmerChosen:farmerChosen}]);
      setSelectedItem( {...modifiedItem,farmerChosen:farmerChosen});

      //  console.log(modifiedItem);
    } else {
  
      setSelectedItem({...found[0],farmerChosen:farmerChosen});
    }
    
    //history.push(`/checkout`);

    history.push(`/item/${row.id}`);
  };

  function numberWithCommas(x) {
    if(x){
      return x?.toString()?.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
    else return " NAN "
  
  }
  return (
    <div style={{ position: "relative",width:'auto'  ,minWidth:'300px', }}>
      {showFav && (
        <div
          style={{
            position: "absolute",
            top: "20px",
            right: "20px",
            cursor: "pointer",
          }}
          onClick={() => {
            onFavClicked();
            setFavorite(!favorite);
          }}
        >
          <div className="d-flex">
            {" "}
            <Icon
              fill={favorite ? "black" : "#fff"}
              name="bookmark"
              size={22}
            />
          </div>
        </div>
      )}
       {showFav && role!='admin'&& (
        <div
          style={{
            position: "absolute",
            top: "5px",
            right: "30px",
            cursor: "pointer",
          }}
          onClick={() => {
            onFavClicked();
            setFavorite(!favorite);
          }}
        >
          <div className="d-flex">
            {" "}
           <p>{favorite ? removeFromFav : addToFav}</p>
          </div>
        </div>
      )}
      {role && role == "admin" && (
        <div
          style={{
            position: "absolute",
            top: "20px",
            right: "60px",
            cursor: "pointer",
          }}
          onClick={() => {
            history.push(`/product/${row.id}`);
          }}
        >
          {" "}
          <Icon name="edit" size={22} />
        </div>
      )}

      {role && role == "admin" && (
        <div
          style={{
            position: "absolute",
            top: "20px",
            right: "100px",
            cursor: "pointer",
          }}
          onClick={() => {
            ondeleteClicked();
          }}
        >
          {" "}
          <Icon name="trash" size={22} />
        </div>
      )}
      <div
        className="cardMain"
        style={{ display: "flex", flexDirection: "column" }}
      >
        <div className="row" style={{ display: "flex", flexDirection: "row", flex: 1 }}>
          <div className="cardImage col-md-4 col-lg-3 col-sm-12 col-xs-12" style={{ flex: 1 }}>
            <img
              className="imageDiv"
              src={image}
              style={{ flex: 2, maxWidth: "150px" }}
            />
          </div>

          <div
            style={{
              display: "flex",
              marginTop: "15px",
              padding: "15px",
              flex: 3,
            
            }}
          >
            <div
              className=""
              style={{ flex: 4, display: "flex", flexDirection: "column" }}
            >
              <span className="heading" style={{ flex: 1 }}>
                {title}
              </span>

              <div
                className="headingIcon"
                style={{ display: "flex", alighItems: "center", flex: 1 }}
              >
                <div
                  style={{
                    display: "flex",
                    alighItems: "center",
                    flexDirection: "column",
                    minWidth:'150px'
                  }}
                >
                  <div
                    className="headingIconText"
                    style={{ fontSize: "16px", lineHeight: "28px" }}
                  >{`GRADE ${grade} (${row.weight} / Piece)`}</div>
                  <span
                    style={{
                      fontSize: "14px",
                      lineHeight: "18px",
                      marginBottom: "15px",
                    }}
                  >
                    {moreInfo}
                  </span>
                </div>
              </div>
              <div style={{ flex: 1 }}></div>

              {!showchilds && (
                <div
                  className=" row "

                  style={{
                    display: "flex",
                    alignItems: "center",
                    flex: 4,
                    background: "#fafafa",
                    padding: "16px 24px",
                    borderRadius: "16px",
                    margin: "0 0 0 -16px",
               
                    width:'100%'
                  }}
                >
                 <div  className="col-md-12 col-lg-6 col-sm-12 col-xs-12 d-flex align-content-center" style={{flex:2,minWidth:'200px',width:'100%' }}>
                 <div style={{ display: "flex", flexDirection: "row",flex:10}}>
                    <div style={{ paddingTop: "5px" }} className="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                      <Icon name="info" size={15} />
                    </div>

                    <div style={{ display: "flex", flexDirection: "column" ,flex:9}}  className="col-lg-9 col-md-12 col-sm-12 col-xs-12">
                      <div className="headingIconText">
                        {row.farmers[0].name} {row.farmers[0].rating} ☆
                      </div>
                      <span style={{ fontSize: "13px" }}>
                        {row.farmers[0].location}
                      </span>
                      <span style={{ fontSize: "15px", color: "#02a78b" }}>
                        Availibilty: {row.farmers[0].availibilty} (
                        {row.farmers[0].interval})
                      </span>
                    </div>
                  </div>
    <div className="col-md-2 col-lg-2 col-sm-2 col-xs-2 d-flex" style={{flex:1}}>
    <img
                    src={require("../assets/images/iso.png").default}
                    style={{ width: "30px",height:'30px' }}
                  />
                  <img
                    src={require("../assets/images/globalgap.png").default}
                    style={{ width: "30px",height:'30px' }}
                  />
    </div>
                   </div>
                  <div style={{ flex: 2 ,minWidth:'250px',justifyContent:'center'}} className="col-md-12 col-lg-6 col-sm-12 col-xs-12 m-2 d-flex">
                    <Button
                      rounded={true}
                      onClick={() => {
                        onClick(row,row.farmers[0]);
                      }}
                      text={`${currency}${numberWithCommas(price)} • KG`}
                      rounded={true}
                      className="btnCard"
                      style={{ flex: 1, marginTop: "10px",maxWidth:'300px' }}
                    />
                  </div>
                 
                </div>
              )}
            </div>
          </div>
        </div>

        <div
          className="cardBtnDiv"
          style={{
            flex: 1,
            padding: "0px 5px",
            marginTop: "0px",
            cursor: "pointer",
          }}
          onClick={oncardclick}
        >
          <div style={{ flex: 5 }}></div>
          <div style={{ display: "flex" }}>
            <span style={{ paddingRight: "10px" }}>View farmers</span>
            {showchilds ? (
              <Icon name="chevron-up" background="#dfdad4" size={25} />
            ) : (
              <Icon name="chevron-down" background="#dfdad4" size={25} />
            )}
          </div>
        </div>
        {showchilds &&
          row.farmers &&
          row.farmers.map((rr) => {
            return (
              <div
                className="headingIcon row"
                style={{
                  display: "flex",
                  alignItems: "center",
                  flex: 1,
                  background: "#fafafa",
                  padding: "16px 24px",
                  borderRadius: "16px",
                  margin: "15px",
                }}
              >
                <div className="col-md-6 col-lg-6 col-sm-12 col-xs-12 m-2" style={{ display: "flex", flexDirection: "row", flex: 3 }}>
                  <div style={{ paddingTop: "5px" }}>
                    <Icon name="info" size={15} />
                  </div>

                  <div style={{ display: "flex", flexDirection: "column" }}>
                    <div className="headingIconText">
                      {rr.name} {rr.rating} ☆
                    </div>
                    <span style={{ fontSize: "13px" }}>{rr.location}</span>
                    <span style={{ fontSize: "15px", color: "#02a78b" }}>
                      Availibilty: {rr.availibilty} ({rr.interval})
                    </span>
                  </div>
                  <img
                  src={require("../assets/images/iso.png").default}
                  style={{ width: "30px",height:'30px' ,marginLeft:'10px'}}
                />
                <img
                  src={require("../assets/images/globalgap.png").default}
                  style={{ width: "30px",height:'30px' ,marginLeft:'10px' }}
                />
                </div>
              
                <div className="col-md-6 col-lg-6 col-sm-12 col-xs-12 m-2" style={{ flex: 1 }}>
                  {rr.price && (
                    <Button
                      rounded={true}
                      onClick={() => {
                        onClick(row,rr);
                      }}
                      text={`${currency}${numberWithCommas(rr.price)} • KG`}
                      rounded={true}
                      className="btnCard"
                      style={{ flex: 1, marginRight: "10px" }}
                    />
                  )}
                </div>
                <div></div>
              </div>
            );
          })}
      </div>
    </div>
  );
}

export default Card;
