import React, { useState, useContext, useEffect, useRef } from "react";
import Screen from "../components/Screen";
import OrderCard from "../components/OrderCard";
import MultiSelect from "../components/MultiSelect";
import MultiRangeSlider from "../components/MultiRangeSlider";
import ModalOption from "../components/ModalOption";
import TextInput from "../components/TextInput";
import TextOption from "../components/TextOption";
import Button from "../components/Button";
import Icon from "../components/Icon";
import Loading from "../components/Loading";
import firebase from "../firebase";
import "firebase/auth";
import "firebase/firestore";
import AuthContext from "../AuthProvider";
import { useHistory } from "react-router-dom";
import AppContext from "../context/AppContext";

import Modal from "react-modal";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    minWidth: "450px",
    padding: "24px",
  },
};
// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
//Modal.setAppElement('#root');

export default function Farmers() {
  const history = useHistory();
  const unsubscribe = useRef(null);
  const [opened, setopened] = useState(false);
  const [data, setData] = useState([]);
  const [dataSelected, setdataSelected] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const [frame, setframe] = useState("main");
  const { deliveryLocations, setdeliveryLocations, setsignoutvisible } =
    useContext(AppContext);

  const ref = firebase.firestore().collection("farmers");

  useEffect(() => {
    try {
      unsubscribe.current = ref.onSnapshot(onCollectionUpdate);
      return () => {
        unsubscribe.current();
      };
    } catch (e) {
      console.log(e);
    }
  }, []);

  useEffect(() => {}, [dataSelected]);

  const changeSelected = () => {
    console.log(dataSelected);
    // setdataSelected([]);
    if (
      !dataSelected.name ||
      !dataSelected.location ||
      !dataSelected.contactNumber ||
      !dataSelected.farmname
    ) {
      alert("Please fill all data");
      return;
    }
    if (
      dataSelected.name &&
      dataSelected.location &&
      dataSelected.contactNumber &&
      dataSelected.farmname &&
      dataSelected.id
    ) {
      const db = firebase.firestore();

      const farmerref = db.collection("farmers");

      farmerref
        .doc(dataSelected.id)
        .update({
          name: dataSelected.name,
          farmname: dataSelected.farmname,
          location: dataSelected.location,
          contactNumber: dataSelected.contactNumber,
        })
        .then((row) => {
          //farmerref.doc(row.id)

          console.log("ok");
          setopened(false);
          return;
        })
        .catch((error) => {
          console.log(error.message);
          // alert(error.message);
        });
    }

    if (
      dataSelected.name &&
      dataSelected.location &&
      dataSelected.contactNumber &&
      dataSelected.farmname &&
      !dataSelected.id
    ) {
      // console.log(dataSelected);
      const db = firebase.firestore();

      const farmerref = db.collection("farmers");

      farmerref
        .add({
          name: dataSelected.name,
          farmname: dataSelected.farmname,
          location: dataSelected.location,
          contactNumber: dataSelected.contactNumber,
        })
        .then((row) => {
          farmerref.doc(row.id).update({ id: row.id });
          // setloading(false);
          // history.push('/');
          console.log("ok");
          setopened(false);
        })
        .catch((error) => {
          console.log(error.message);
          // alert(error.message);
        });
    }
  };
  const onCollectionUpdate = (querySnapshot) => {
    setData(querySnapshot?.docs?.map((doc) => doc.data()));
    setIsLoading(false);
  };

  const deleteFarmer = (key) => {
    try {
      firebase
        .firestore()
        .collection("farmers")
        .doc(key)
        .delete()
        .then((result) => {
          // alert('ok');
          //console.warn(result);
        })
        .catch((error) => {
          console.warn(error);
        });
    } catch (e) {
      console.log(e);
    }
  };

  const editFarmer = (row) => {
    setdataSelected(row);
    setopened(true);
    setframe("addfarmer");
  };

  return (
    <>
      {isLoading && <Loading />}
      <ModalOption
        modalIsVisible={opened}
        setmodalIsVisible={setopened}
        frameUsed={frame}
        setframeUsed={setframe}
        dataSelected={dataSelected}
        setdataSelected={setdataSelected}
        changeSelected={changeSelected}
      />

      <div className="container-flex" style={{ marginTop:'160px' }}>
        <div style={{ flex: 3 }} className="listMain">
          <div style={{ display: "flex" }}>
            <div style={{ flex: 3 }}>
              <h2>Farmers</h2>
            </div>
            <div style={{ flex: 1 }}>
              <Button
                onClick={() => {
                  setdataSelected([]);
                  setopened(true);
                  setframe("addfarmer");
                }}
                text="Add farmer"
              />
            </div>
          </div>

          {data &&
            data.map((d) => {
              return (
                <TextOption
                  key={d.id}
                  iconAlign="left"
                  icon="user"
                  text={d.name}
                  subText={
                    "Location:" +
                    d.location +
                    " - " +
                    "Farm name:" +
                    d.farmname +
                    " - " +
                    "Contact number:" +
                    d.contactNumber
                  }
                  btnText="Edit"
                  onClick={() => {
                    editFarmer(d);
                  }}
                  frameUsed="time"
                  secondAction="trash"
                  onClicksecondAction={() => {
                    deleteFarmer(d.id);
                  }}
                />
              );
            })}
          {data === undefined && <p>No farmers yet </p>}
        </div>
      </div>
    </>
  );
}
