import React,{useState,useContext,useEffect} from "react";
import Icon from '../components/Icon';


function MultiSelect({title,listtofilter,setlisttofilter,setfiltersApplied,arrayfilter,setarrayfilter,onChange}) {


const setSelected = (rowselected) =>{


  const newlist = listtofilter.map((row)=>{
    if(rowselected.value === row.value)
    {

      return {...row,isselected:!row.isselected};

    }
    else{
      return row;
    }
  });

//  setfiltersApplied(filters);
  setlisttofilter(newlist);
// setarrayfilter([]);
// newlist.filter((tr)=>{
//   if(tr.isselected){
//     setarrayfilter([...arrayfilter, tr.value]);
//   }
//
// });
 //console.log(arrayfilter);
}

useEffect(()=>{
  const fff = listtofilter.filter((row)=>{
    if(row.isselected){
      return row.value;
    }


  })
  const rrrr = fff.map((aa)=>{
    return aa.value
  })
  setarrayfilter(rrrr)

},[listtofilter])

  const FilterItem = ({icon,checkselection,onClick,text = "A"}) =>{
    return (
      <>

      <div className={`noselect menuOption ${checkselection ? 'selected' : ''}`} style={{display:'flex',margin:'10px 10px 10px 0px'}} onClick={onClick}>

        { icon &&
           <>
          <div style={{flex:1}}>
              <Icon className="clickable" name={`fa-map-marker `} size={15}/>
          </div>
          <div className="spacer">

          </div>
          </>
}
          <div className="optionText">
          {text}
          </div>


        </div>
        </>
    )
  }

  return (
    <>
  { title && <h3 style={{ paddingLeft:"5px"}}>{title}</h3>}
<div style={{display:'flex',flexWrap:'wrap',flexDirection:'row'}}>
{
  listtofilter.map((row,i)=>{
    return <FilterItem key={i} text={row.label} onClick={()=>{setSelected(row)}} checkselection={row.isselected} icon={row.icon}/>
  })
}

</div>
</>
  );
}

export default MultiSelect;
