import React, { useState } from "react";
import Icon from "../components/Icon";
import Button from "../components/Button";
import QuantityButton from "../components/QuantityButton";
import { useHistory } from "react-router-dom";

// <QuantityButton text="$3 / KG"  rounded={true} className="qunatitybtnCard"/>
function OrderCard({
  ordermain,
  id,
  date,
  location,
  status,
  text,
  onClick,
  childs,
  role,
  onAccept,
  onReject,
  onReorder,
  canAcceptDecline,
}) {
  const history = useHistory();
  const [showchilds, setshowchilds] = useState(false);
  const oncardclick = () => {
    setshowchilds(!showchilds);
  };
  return (
    <>
      <div className="cardMain">
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            padding: "15px",
            flex: 1,
          }}
        >
          <div style={{ display: "flex", marginTop: "15px" }} className="row d-flex justify-content-center">
            <div
              className="heading col-md-4 col-lg-4 col-sm-12 col-xs-12"
              style={{ flex: 4 ,minWidth:'300px',width:'auto'}}
            >{`Order ${childs[0].name.slice(0,10)} ...`}</div>
            <div className="col-md-2 col-lg-2 col-sm-3 col-xs-3" style={{ minWidth:'200px'}}>
            <Button
              text={status}
              rounded={true}
       
              style={{
                flex: 3,
                fontSize: "20px",
                backgroundColor: "#FFF",
                color: "#02a78b",
              }}
            />
            </div>
         
        {   role != 'farmer' && <div className="col-md-2 col-lg-2 col-sm-3 col-xs-3" style={{
                minWidth: "40px",
                maxHight: "10px"
                }}>
            <Button
              text=""
              icon="repeat"
              rounded={true}
              className="btnCard col-md-2 col-lg-2 col-sm-3 col-xs-3 m-2"
              style={{
                flex: 1,
                fontSize: "14px",
                
              }}
              onClick={() => {
                history.push(`/checkout/${id}`);
              //  onReorder();
              }}
            />
             </div>}
            {(status == "Draft" && role != 'farmer') && (
              <div
              className="col-md-3 col-lg-3 col-sm-3 col-xs-3"
                style={{
                  marginTop: "10px",
                  marginBottom: "10px",
                  top: "20px",
                  right: "60px",
                  cursor: "pointer",
                  width:'70px'
                }}
                onClick={() => {
                  history.push(`/checkout/${id}`);
                }}
              >
                {" "}
                <Icon name="edit" size={22} />
              </div>
            )}
          </div>

          {(status == "Draft" ||
            status == "Recurring Requests" ||
            status == "Sent" || status ==  'Pending Approval' ) &&
            canAcceptDecline && (
              <div style={{ display: "flex" }}>
                <span
                  style={{ fontSize: "14px", flex: 1 }}
                >{`Order #${id}`}</span>
                {((role && role == "admin") || role == "farmer") && (
                  <div style={{ flex: 1, display: "flex", marginTop: "10px" }}>
                    <Button
                      text="Accept"
                      rounded={true}
                      className="btnCard"
                      style={{
                        flex: 1,
                        fontSize: "14px",
                        width: "20px",
                        margin: "10px",
                      }}
                      onClick={onAccept}
                    />
                    <Button
                      text="Reject"
                      rounded={true}
                      className="btnCard"
                      style={{
                        flex: 1,
                        fontSize: "14px",
                        width: "20px",
                        backgroundColor: "#b73636",
                        margin: "10px",
                      }}
                      onClick={onReject}
                    />
                  </div>
                )}
              </div>
            )}
          <div className="subHeading">{date}</div>
          <div
            className="headingIcon row"
            style={{ display: "flex", alighItems: "center" }}
          >
            <div className="col-md-6 col-lg-6 col-sm-12 col-xs-12" style={{ flex: 6 }}>
              <div
                className="headingIconText"
                style={{ display: "flex", alignItems: "center" }}
              >
                <Icon style={{ flex: 1 }} name="truck" size={15} />
                <div className="headingIconText" style={{ flex: 1 }}>
                  {" "}
                  Delivered to {location}
                </div>
              </div>
              {role && role == "admin" && (
                <div
                  className="headingIconText"
                  style={{ flex: 1, display: "flex", flexDirection: "column" }}
                >
                  {ordermain.author && ordermain.author.userName && (
                    <div className="" style={{ fontSize: "13px", flex: 1 }}>
                      User: {ordermain.author.userName}{" "}
                    </div>
                  )}
                  {ordermain.author && ordermain.author.email && (
                    <div className="" style={{ fontSize: "13px", flex: 1 }}>
                      Email: {ordermain.author.email}{" "}
                    </div>
                  )}
                </div>
              )}
            </div>
            <div className="col-md-6 col-lg-6 col-sm-12 col-xs-12" style={{ display: "flex", alignItems: "center" }}>
              <span
                style={{
                  flex: 1,
                  paddingRight: "10px",
                  cursor: "pointer",
                  userSelect: "none",
                  textAlign:"end"
                }}
                onClick={oncardclick}
              >
                View details
              </span>
              {showchilds ? (
                <Icon
                  style={{ flex: 1 ,cursor: "pointer"}}
                  name="chevron-up"
                  background="#dfdad4"
                  size={25}
                />
              ) : (
                <Icon
                  style={{ flex: 1 ,cursor: "pointer"}}
                  name="chevron-down"
                  background="#dfdad4"
                  size={25}
                />
              )}
            </div>
          </div>
          <div
            className="cardBtnDiv"
            style={{ padding: "0px 5px", marginTop: "10px", display: "none" }}
          >
            <div style={{ flex: 5, padding: "10px" }}></div>
          </div>
        </div>
      </div>
      {showchilds &&
        childs &&
        childs.map((row) => {
          return (
            <div
              key={row.id}
              className="cardMain"
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                padding: "20px",
                background: "#e8e8e8",
              }}
              onClick={onClick}
            >
              <div
                style={{ display: "flex", flexDirection: "column", flex: 3 }}
              >
                <div style={{ display: "flex" }}>
                  <div
                    className=""
                    style={{ flex: 4, fontWeight: "bold", margin: "10px 0px" }}
                  >
                    {row.name}
                  </div>
                  <br />
                </div>

                <div
                  className=""
                  style={{
                    display: "flex",
                    alighItems: "center",
                    flexDirection: "column",
                  }}
                >
                  <div
                    className=""
                    style={{ fontSize: "13px", margin: "10px 0px" }}
                  >
                    {row.quantity} KG - {`LBP${row?.farmerChosen?.value?.price?row?.farmerChosen?.value?.price:'NaN'}`}{" "}
                  </div>
                  <div className="" style={{ fontSize: "13px" }}>
             {     row?.location?.label?  'Deliver to '+ row?.location?.label:'No Location Selected'}
                  </div>
                </div>
              </div>

              <div
                className="d"
                style={{
                  flex: 1,
                  alighItems: "center",
                  justifyContent: "center",
                }}
              >
                <span style={{ fontSize: "13px", flex: 1 }}></span>
              </div>
            </div>
          );
        })}
    </>
  );
}

export default OrderCard;
