import React, { useState, useEffect } from "react";
import AppContext from "./context/AppContext";
import AuthContext from "./AuthProvider";
import AppNavigation from "./navigation/AppNavigation";
import Header from "./screens/Header";
import Footer from "./screens/Footer";
import Home from "./screens/Home";
import Screen from "./components/Screen";
import { HashRouter, useHistory } from "react-router-dom";
import firebase from "./firebase";
import './styles/responsive.css'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

export default function App() {
  const [auth, setauth] = useState(false);
  const [visible, setvisible] = useState("hide");
  const [actionsVisible, setactionsVisible] = useState(false);
  const [deliverTo, setdeliverTo] = useState("Pick a location");
  const [datetime, setdatetime] = useState("Now");
  const [timevalue, settimevalue] = useState(null);
  const [repeatvalue, setrepeatvalue] = useState("Doesn`t repeat");
  const [currency, setcurrency] = useState("LBP");
  const [quantity, setQuantity] = useState(0);
  const [totalPrice, setTotalPrice] = useState(0.0);
  const [deliveryTotal, setdeliveryTotal] = useState(100000.0);
  const [selectedItem, setSelectedItem] = useState(null);
  const [items, setItems] = useState([]);
  const [orderBy, setorderBy] = useState("price-asc");
  const [splitDelivery, setsplitDelivery] = useState(false);
  const [remainingQuantity, setRemainingQuantity] = useState(0);
  const [deliveryLocations, setdeliveryLocations] = useState([]);
  const [signoutvisible, setsignoutvisible] = useState(false);
  const [searchedText, setsearchedText] = useState(null);

  const [greaterthan, setgreaterthan] = useState(1000);
  const [lessthan, setlessthan] = useState(25000);

  const [categoriesfilter, setcategoriesfilter] = useState([]);
  const [user, setUser] = useState(null);
  const [userName, setUserName] = useState(null);
  const [userEmail, setUserEmail] = useState(null);
  const [role, setRole] = useState(null);
  const [loadingAuthState, setLoadingAuthState] = useState(true);

  const [mainInterface, setmainInterface] = useState("Crops");
  useEffect(() => {
    firebase.auth().onAuthStateChanged((user) => {
      setUser(user);

      try {
        const db = firebase.firestore();
        db.collection("Users")
          .doc(user.uid)
          .get()
          .then((querySnapshot) => {
            const role = querySnapshot.data().role;
            setRole(role);
            //console.log(querySnapshot.data());
          });
      } catch (e) {
        // console.log(e);
      }

      //  console.log(user);

      setLoadingAuthState(false);
    });
  }, []);
  return (
    <AuthContext.Provider
      value={{
        user,
        authenticated: user !== null,
        setUser,
        loadingAuthState,
        setLoadingAuthState,
      }}
    >
      <AppContext.Provider
        value={{
          auth,
          setauth,
          visible,
          setvisible,
          actionsVisible,
          setactionsVisible,
          signoutvisible,
          setsignoutvisible,
          deliverTo,
          setdeliverTo,
          datetime,
          setdatetime,
          timevalue,
          settimevalue,
          currency,
          quantity,
          setQuantity,
          totalPrice,
          setTotalPrice,
          selectedItem,
          setSelectedItem,
          items,
          setItems,
          deliveryTotal,
          setdeliveryTotal,
          orderBy,
          setorderBy,
          splitDelivery,
          setsplitDelivery,
          remainingQuantity,
          setRemainingQuantity,
          deliveryLocations,
          setdeliveryLocations,
          repeatvalue,
          setrepeatvalue,
          greaterthan,
          setgreaterthan,
          lessthan,
          setlessthan,
          role,
          setRole,
          searchedText,
          setsearchedText,
          userName,
          setUserName,
          userEmail,
          setUserEmail,
          mainInterface,
          setmainInterface,
        }}
      >
        <HashRouter>
          <>
            <Screen>
              <Header />

              <AppNavigation />

              <Footer />
            </Screen>
          </>
        </HashRouter>
      </AppContext.Provider>
    </AuthContext.Provider>
  );
}
