import React, { useState, useContext } from "react";
import TextInput from "../components/TextInput";
import Button from "../components/Button";
import "react-phone-number-input/style.css";
import PhoneInput, { isValidPhoneNumber } from "react-phone-number-input";
import { useHistory } from "react-router-dom";
import firebase from "../firebase";
import "firebase/auth";
import "firebase/firestore";
import AuthContext from "../AuthProvider";

const Register = () => {
  const authContext = useContext(AuthContext);
  const { loadingAuthState, setLoadingAuthState } = useContext(AuthContext);
  const [phonenumber, setphonenumber] = useState("");
  const [values, setValues] = useState({
    email: "",
    password: "",
    phone: "",
    firstName: "",
    lastName: "",
    repeatPassword: "",
  });

  const history = useHistory();
  const handleClick = () => {
    history.push("/");
  };
  const handleChange = (event) => {
    event.persist();

    setValues((values) => ({
      ...values,
      [event.target.name]: event.target.value,
    }));
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    if (!handleValidation()) {
      return;
    }
    console.log(values, "values");
    try {
      firebase
        .auth()
        .createUserWithEmailAndPassword(values.email, values.password)
        .then((userCredential) => {
          console.log("test");
          console.log(userCredential);
          authContext.setUser(userCredential);
          const db = firebase.firestore();
          db.collection("Users")
            .doc(userCredential.user.uid)
            .set({
              id: userCredential.user.uid,
              email: values.email,
              role: "vendor",
              phone: phonenumber,
              firstName: values.firstName,
              lastName: values.lastName,
            })
            .then(() => {
              console.log("ok");
              history.push("/");
            })
            .catch((error) => {
              alert(error);
              console.log(error.message);
              // alert(error.message);
            });
        })
        .catch((error) => {
          setLoadingAuthState(false);
          let errorsNew = {};
          errorsNew["incorrect"] =
            "The email address is already in use by another account.";
          seterrorss(errorsNew);
        });
    } catch (e) {
      setLoadingAuthState(false);
      let errorsNew = {};
      errorsNew["incorrect"] =
        "The email address is already in use by another account.";
      seterrorss(errorsNew);
    }
  };

  const [errorss, seterrorss] = useState({});

  const handleValidation = () => {
    let fields = values;
    let errors = {};
    let formIsValid = true;

    //Name

    if (!fields["firstName"]) {
      formIsValid = false;
      errors["firstName"] = "Cannot be empty";
    }

    if (!fields["lastName"]) {
      formIsValid = false;
      errors["lastName"] = "Cannot be empty";
    }

    // if(typeof fields["name"] !== "undefined"){
    //    if(!fields["name"].match(/^[a-zA-Z]+$/)){
    //       formIsValid = false;
    //       errors["name"] = "Only letters";
    //    }
    // }

    //Email
    if (!fields["email"]) {
      formIsValid = false;
      errors["email"] = "Cannot be empty";
    }

    if (phonenumber === "") {
      formIsValid = false;
      errors["phone"] = "Cannot be empty";
    }

    if (!fields["password"]) {
      formIsValid = false;
      errors["password"] = "Cannot be empty";
    }
    if (!fields["repeatPassword"]) {
      formIsValid = false;
      errors["repeatPassword"] = "Cannot be empty";
    }

    if (fields["password"] !== fields["repeatPassword"]) {
      formIsValid = false;
      errors["repeatPassworderror"] = "Passwords doesn't match";
    }
    //
    // if(/^\d{14}$/.test(phonenumber) ===false){
    //
    //   errors["phone"] = "Phone number is not valid";
    // }
    // if(isValidPhoneNumber(phonenumber)){
    //   formIsValid = false;
    //     errors["phone"] = "Phone number is invalid";
    // }

    if (typeof fields["email"] !== "undefined") {
      let lastAtPos = fields["email"].lastIndexOf("@");
      let lastDotPos = fields["email"].lastIndexOf(".");

      if (
        !(
          lastAtPos < lastDotPos &&
          lastAtPos > 0 &&
          fields["email"].indexOf("@@") == -1 &&
          lastDotPos > 2 &&
          fields["email"].length - lastDotPos > 2
        )
      ) {
        formIsValid = false;
        errors["email"] = "Email is not valid";
      }
    }

    seterrorss(errors);
    return formIsValid;
  };

  const handlephone = (val) => {
    console.log(val);
    setphonenumber(val);
  };
  return (
    <div className="portable">
      <label>Create an account</label>
      {errorss["incorrect"] && (
        <span className="error">{errorss["incorrect"]}</span>
      )}
      <form onSubmit={handleSubmit}>
        <div style={{ marginBottom: "10px" }} className="txtMainDiv">
          <PhoneInput
            defaultCountry="LB"
            placeholder="Enter phone number"
            value={values.phone}
            onChange={(val) => {
              handlephone(val);
            }}
            name="phone"
            className="txtphone"
          />
        </div>
        {errorss["phone"] && <span className="error">{errorss["phone"]}</span>}
        <div style={{ marginBottom: "10px" }}>
          <TextInput
            placeholder="Enter your Email"
            onChange={handleChange}
            text={values.email}
            name="email"
          />
          {errorss["email"] && (
            <span className="error">{errorss["email"]}</span>
          )}
        </div>

        <div style={{ marginBottom: "10px" }}>
          <TextInput
            placeholder="Enter first name"
            onChange={handleChange}
            text={values.firstName}
            name="firstName"
          />
          {errorss["firstName"] && (
            <span className="error">{errorss["firstName"]}</span>
          )}
        </div>
        <div style={{ marginBottom: "10px" }}>
          <TextInput
            placeholder="Enter last name"
            onChange={handleChange}
            text={values.lastName}
            name="lastName"
          />
          {errorss["lastName"] && (
            <span className="error">{errorss["lastName"]}</span>
          )}
        </div>
        <div style={{ marginBottom: "10px" }}>
          <TextInput
            type="password"
            placeholder="Enter your Password"
            onChange={handleChange}
            text={values.password}
            name="password"
          />
          {errorss["password"] && (
            <span className="error">{errorss["password"]}</span>
          )}
        </div>
        <div style={{ marginBottom: "10px" }}>
          <TextInput
            type="password"
            placeholder="Repeat your Password"
            onChange={handleChange}
            text={values.repeatPassword}
            name="repeatPassword"
          />
          {errorss["repeatPassword"] && (
            <span className="error">{errorss["repeatPassword"]}</span>
          )}
        </div>

        {errorss["repeatPassworderror"] && (
          <span className="error">{errorss["repeatPassworderror"]}</span>
        )}
        <Button
          onClick={(e) => {
            handleSubmit(e);
          }}
          style={{ marginTop: "15px" }}
          text="Sign Up"
        />
        <div className="textCenter" style={{ marginTop: "8px" }}>
          <label onClick={handleClick}>Already have account? Login</label>
        </div>
      </form>
    </div>
  );
};
export default Register;
