import React,{useState} from 'react';
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from 'react-places-autocomplete';

export default function MapsInput({setaddressValue}) {
  const [address,setaddress] = useState('');
const   handleChange = (address) => {
    setaddress(address);
  //  setaddressValue(address);
  };

const  handleSelect = (address) => {

    geocodeByAddress(address)
      .then(results => setaddressValue(results[0]))
      .then(latLng => console.log('Success', latLng))
      .catch(error => console.error('Error', error));
    //  setaddressValue(address);
      setaddress(address);
  };
  const searchOptions = {
    componentRestrictions: { country: ['lb'] },

  }

    return (
      <PlacesAutocomplete
        value={address}
        onChange={handleChange}
        onSelect={handleSelect}
    searchOptions={searchOptions}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
          <div >
          <div className="txtMainDiv">
            <input
              {...getInputProps({
                placeholder: 'Search Places ...',
                className: 'txtMain',
              })}
            />
            </div>
          {loading || suggestions.length != 0  &&  <div className="autocomplete-dropdown-container" style={{backgroundColor:"#fff",padding:"5px",border:"1px solid #F6F6F6",position:"absolute",minWidth:"350px"}}>
              {loading && <div>Loading...</div>}
              {suggestions.map(suggestion => {
                const className = suggestion.active
                  ? 'suggestion-item--active'
                  : 'suggestion-item';
                // inline style for demonstration purpose
                const style = suggestion.active
                  ? { backgroundColor: '#fafafa', cursor: 'pointer',padding:"10px" }
                  : { backgroundColor: '#ffffff', cursor: 'pointer',padding:"10px" };
                return (
                  <div
                    {...getSuggestionItemProps(suggestion, {
                      className,
                      style,
                    })}
                  >
                    <span >{suggestion.description}</span>
                  </div>
                );
              })}
            </div>}
          </div>
        )}
      </PlacesAutocomplete>
    );

}
