import React, { useState, useContext, useRef, useEffect } from "react";
import Screen from "../components/Screen";
import AppContext from "../context/AppContext";
import AuthContext from "../AuthProvider";
import OrderCard from "../components/OrderCard";
import MultiSelect from "../components/MultiSelect";
import MultiRangeSlider from "../components/MultiRangeSlider";
import categories from "../assets/static/categories";
import firebase from "../firebase";
import "firebase/firestore";

import Loading from "../components/Loading";
import ModalOption from "../components/ModalOption";

// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
//Modal.setAppElement('#root');

export default function Orders() {
  const [opened, setopened] = useState(false);
  const [frame, setframe] = useState("main");

  const [arrayfilter, setarrayfilter] = useState([]);
  const [schedulefilter, setschedulefilter] = useState([]);
  const [locationfilter, setlocationfilter] = useState([]);
  const [selectedlocationfilter, setselectedlocationfilter] = useState([]);
  const [selectedschedulefilter, setselectedschedulefilter] = useState([]);

  const [categoryarrayfilter, setcategoryarrayfilter] = useState([]);
  const list = [
    // {value:"Order Placed", label:"Placed",isselected:false},
    // {value:"Order rejected", label:"Rejected",isselected:false},
    // {value:"Order accepted", label:"Accepted",isselected:false}

    {
      value: "Purchase Scheduled",
      label: "Purchase Scheduled",
      isselected: false,
    },
    { value: "Delivered", label: "Delivered", isselected: false },
    { value: "Paid", label: "Paid", isselected: false },
    { value: "Rejected", label: "Rejected", isselected: false },
    { value: "Accepted", label: "Accepted", isselected: false },
  ];

  const [listtofilter, setlisttofilter] = useState(list);

  const gardefilters = [
    { value: "A", label: "A", isselected: false },
    { value: "B", label: "B", isselected: false },
    { value: "C", label: "C", isselected: false },
  ];

  useEffect(() => {
    changedFilter();
    //console.log(categoryarrayfilter);
  }, [
    arrayfilter,
    categoryarrayfilter,
    selectedschedulefilter,
    setselectedlocationfilter,
    selectedlocationfilter,
  ]);

  const changedFilter = () => {
    // console.log(arrayfilter );
    list.map((filter) => {
      unsubscribe.current = ref
        .where("status", "==", filter.value)
        .onSnapshot(onCollectionUpdate);
    });
  };
  const [gradetofilter, setgradetofilter] = useState(gardefilters);

  const certificationslist = [
    { value: "10", label: "ISO", isselected: false },
    { value: "20", label: "GLOBAL GAP", isselected: false },
  ];

  const [certifications, setcertifications] = useState(certificationslist);

  const ratingList = [
    { value: "3", label: "3 ☆ ", isselected: false },
    { value: "3.5", label: "3.5 ☆", isselected: false },
    { value: "4", label: "4 ☆", isselected: false },
    { value: "4.5", label: "4.5 ☆", isselected: false },
    { value: "5", label: "5 ☆", isselected: false },
  ];

  const [ratings, setratings] = useState(ratingList);

  const [categorylist, setcategorylist] = useState(categories);
  const { setactionsVisible, setsignoutvisible, role } = useContext(AppContext);
  const { user } = useContext(AuthContext);

  const [isLoading, setIsLoading] = React.useState(true);

  const formatDate = (value) => {
    let date = new Date(value);
    const day = date.toLocaleString("default", { day: "2-digit" });
    const month = date.toLocaleString("default", { month: "short" });
    const year = date.toLocaleString("default", { year: "numeric" });
    return day + " " + month + ", " + year;
  };

  const ref =
    role && (role == "admin" || role == "farmer")
      ? firebase
          .firestore()
          .collection("restaurant_orders")
          .orderBy("createdAt", "desc")
      : //: firebase.firestore().collection("restaurant_orders").orderBy('createdAt', 'desc'));;
        firebase
          .firestore()
          .collection("restaurant_orders")
          .where("authorID", "==", user.uid)
          .orderBy("createdAt", "desc");

  const unsubscribe = useRef(null);
  const [data, setData] = useState([]);

  useEffect(() => {
    list.map((filter) => {
      unsubscribe.current = ref
        .where("status", "==", filter.value)
        .onSnapshot(onCollectionUpdate, (error) => {
          console.log(error);
        });
    });
    setsignoutvisible(false);
  }, []);

  useEffect(() => {
    setactionsVisible(false);
    return () => {
      unsubscribe.current();
    };
  }, []);

  useEffect(() => {
    let array = [];
    let locArray = [];
    setschedulefilter([]);
    setlocationfilter([]);
    list.map((filter) => {
      ref.where("status", "==", filter.value).onSnapshot((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const docData = doc.data();
          let date = new Date(
            docData?.createdAt?.seconds
              ? docData?.createdAt?.seconds * 1000
              : 1000
          );

          let locationfilter = {
            value:
              docData?.products.length > 0
                ? docData?.products[0]?.location?.value
                  ? docData?.products[0]?.location?.value?.title +
                    " " +
                    docData?.products[0]?.location?.value?.subText
                  : docData?.products[0]?.location
                : "No delivery location",
            label:
              docData?.products.length > 0
                ? docData?.products[0]?.location?.value
                  ? docData?.products[0]?.location?.value?.title +
                    " " +
                    docData?.products[0]?.location?.value?.subText
                  : docData?.products[0]?.location
                : "No delivery location",
            isselected: false,
          };
          // if (
          //   locationfilter.indexOf({
          //     value: docData?.address?.city,
          //     label: docData?.address?.city,
          //     isselected: false,
          //   }) < 0
          // ) {
          //   setlocationfilter((prev) => [
          //     ...prev,
          //     {
          //       value: docData?.address?.city,
          //       label: docData?.address?.city,
          //       isselected: false,
          //     },
          //   ]);
          // }

          let boolloc = false;
          locArray.length == 0 && locArray.push(locationfilter);
          locArray.length > 0 &&
            locArray.map((res) => {
              if (res.value == locationfilter.value) {
                boolloc = true;
              }
            });
          if (!boolloc) {
            locArray.push(locationfilter);
          }
          setlocationfilter(locArray);

          let datefilter = {
            value: date.toDateString().toString(),
            label: date.toDateString().toString(),
            isselected: false,
          };

          //   array.indexOf(datefilter) === -1 ? array.push(datefilter) : console.log("This item already exists");

          //  for(let i = 1; i<array.length;i++){
          //   alert(array[i].value==array[i-1].value)
          //  }
          let bool = false;
          array.length == 0 && array.push(datefilter);
          array.length > 0 &&
            array.map((res) => {
              if (res.value == datefilter.value) {
                bool = true;
              }
            });
          if (!bool) {
            array.push(datefilter);
          }

          setschedulefilter(array);
        });
      });
    });
  }, []);
  const dataArray = [];
  const onCollectionUpdate = (querySnapshot, index) => {
    if (index == 0) {
      dataArray = [];
    }
    //  setLoading(true);

    querySnapshot.forEach((doc) => {
      const docData = doc.data();

      const { status } = doc.data();

      if (arrayfilter && arrayfilter.length !== 0) {
        if (arrayfilter.indexOf(status) >= 0) {
          dataArray.push({
            id: doc.id,
            ...docData,
          });
        }
      } else {
        dataArray.push({
          id: doc.id,
          ...docData,
        });
      }
    });

    const tempData1 = [];
    dataArray.forEach((docData) => {
      const { createdAt } = docData;
      let date = new Date(createdAt.seconds ? createdAt.seconds * 1000 : 1000);
      if (selectedschedulefilter && selectedschedulefilter.length !== 0) {
        if (selectedschedulefilter.indexOf(date.toDateString()) >= 0) {
          tempData1.push({
            id: docData.id,
            ...docData,
          });
        }
      } else {
        tempData1.push({
          id: docData.id,
          ...docData,
        });
      }
    });
    const tempData = [];

    tempData1.forEach((docData) => {
      if (selectedlocationfilter && selectedlocationfilter.length !== 0) {
        if (
          selectedlocationfilter.indexOf(
            docData?.products.length > 0
              ? docData?.products[0]?.location?.value
                ? docData?.products[0]?.location?.value?.title +
                  " " +
                  docData?.products[0]?.location?.value?.subText
                : docData?.products[0]?.location
              : "No delivery location"
          ) >= 0
        ) {
          tempData.push({
            id: docData.id,
            ...docData,
          });
        }
      } else {
        tempData.push({
          id: docData.id,
          ...docData,
        });
      }
    });
    // data.forEach((doc) => {
    //   const { products } = doc;
    //   const { name } = products[0];

    //   if (categoryarrayfilter && categoryarrayfilter.length !== 0) {
    //     const contains = categoryarrayfilter.some((element) => {

    //       if (name.includes(element)) {
    //         tempData.push(doc);
    //       }
    //     });
    //   } else {
    //     tempData.push(doc);
    //   }
    // });

    setData(tempData);
    setIsLoading(false);
  };

  const acceptOrder = (id) => {
    try {
      const db = firebase.firestore();

      db.collection("restaurant_orders")
        .doc(id)
        .update({
          status: "Accepted",
        })
        .then(() => {
          changedFilter();
          //Send notification push token
        })
        .catch((error) => {
          console.log(error.message);
          // alert(error.message);
        });
    } catch (e) {
      console.log(e);
    }
  };

  const rejectOrder = (id) => {
    try {
      const db = firebase.firestore();

      db.collection("restaurant_orders")
        .doc(id)
        .update({
          status: "Rejected",
        })
        .then(() => {
          changedFilter();
          //Send notification push token
        })
        .catch((error) => {
          console.log(error.message);
          // alert(error.message);
        });
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    data.map((row) => {
      //row.createdAt.toDate().toDateString()
      //alert(JSON.stringify(row.createdAt))
    });
  }, [data]);

  return (
    <>
      <ModalOption
        modalIsVisible={opened}
        setmodalIsVisible={setopened}
        frameUsed={frame}
        setframeUsed={setframe}
      />
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          marginTop: "130px",
          padding: "20px 80px 0px 80px",
          background: "rgb(250, 250, 250)",
        }}
      >
        {/* <div style={{flex:4}}>
            <h2 style={{fontFamily:"Graphik-Medium",marginBottom:"10px",fontSize:"19px"}}>{`Your requests`}</h2>
    </div> */}
      </div>
      <div className="container-flex row" style={{ marginTop: "0px" }}>
        <div
          style={{
            flex: 1,
            padding: "0px 20px 0px 0px",
            borderRadius: "16px",
            minWidth: "300px",
          }}
          className="hiddenS col-md-3 col-lg-3 col-sm-12 col-xs-12"
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <div style={{ flex: 1 }}>
              <h3>Request Status</h3>
            </div>
            <div
              style={{
                flex: 1,
                marginLeft: "auto",
                textAlign: "right",
                userSelect: "none",
                marginBottom: "20px",
              }}
            >
              <span
                style={{
                  fontSize: "13px",
                  fontWeight: "400",
                  paddingRight: "20px",
                }}
              >
                Select multiple
              </span>
            </div>
          </div>

          <div className="filterSection">
            <MultiSelect
              listtofilter={listtofilter}
              setlisttofilter={setlisttofilter}
              arrayfilter={arrayfilter}
              setarrayfilter={setarrayfilter}
            />
          </div>
          <div className="splitter"></div>

          <div className="filterSection">
            <MultiSelect
              title="Delivery Schedule"
              listtofilter={schedulefilter}
              setlisttofilter={setschedulefilter}
              arrayfilter={selectedschedulefilter}
              setarrayfilter={setselectedschedulefilter}
            />
          </div>
          <div className="filterSection">
            <MultiSelect
              title="Delivery Location"
              listtofilter={locationfilter}
              setlisttofilter={setlocationfilter}
              arrayfilter={selectedlocationfilter}
              setarrayfilter={setselectedlocationfilter}
            />
          </div>
          <div className="splitter"></div>
        </div>

        <div
          style={{ flex: 3, minWidth: "350px" }}
          className="listMain col-md-9 col-lg-9 col-sm-12 col-xs-12"
        >
          {isLoading && <Loading />}

          {data.length === 0 && <span>No requests palced yet.</span>}
          {data.map((row, i) => {
            return (
              <>
                {role == "farmer" ? (
                  row.status == "Draft" ? null : (
                    <OrderCard
                      canAcceptDecline
                      key={i}
                      childs={row.groupedItems[0].childs}
                      status={
                        role == "farmer" && row.status == "Sent"
                          ? "Pending Approval"
                          : row.status
                      }
                      id={row.id}
                      location={
                        row?.products.length > 0
                          ? row?.products[0]?.location?.value
                            ? row?.products[0]?.location?.value?.title +
                              " " +
                              row?.products[0]?.location?.value?.subText
                            : row?.products[0]?.location
                          : ""
                      }
                      date={row.createdAt.toDate().toDateString()}
                      role={role}
                      onAccept={() => {
                        acceptOrder(row.id);
                      }}
                      onReject={() => {
                        rejectOrder(row.id);
                      }}
                      ordermain={row}
                    />
                  )
                ) : (
                  <OrderCard
                    canAcceptDecline
                    key={i}
                    childs={row.groupedItems[0].childs}
                    status={
                      role == "farmer" && row.status == "Sent"
                        ? "Pending Approval"
                        : row.status
                    }
                    id={row.id}
                    location={
                      row?.products.length > 0
                        ? row?.products[0]?.location?.value
                          ? row?.products[0]?.location?.value?.title +
                            " " +
                            row?.products[0]?.location?.value?.subText
                          : row?.products[0]?.location
                        : ""
                    }
                    date={row.createdAt.toDate().toDateString()}
                    role={role}
                    onAccept={() => {
                      acceptOrder(row.id);
                    }}
                    onReject={() => {
                      rejectOrder(row.id);
                    }}
                    ordermain={row}
                  />
                )}
              </>
            );
          })}
        </div>
      </div>
    </>
  );
}
